import service from '../../service.js';
import {
	mapState,
	mapMutations
} from 'vuex'
import mInput from '../../components/m-input.vue'

import '../../common/base64.js';

var Base64 = require('../../common/base64.js');


var http;
export default {
	components: {
		mInput
	},
	data() {
		return {
			providerList: [],
			hasProvider: false,
			account: '',
			password: '',
			positionTop: 0,
			isDevtools: false,
			btTitle: "登录",
			code: ""
		}
	},
	computed: mapState(['forcedLogin']),
	methods: {
		...mapMutations(['login']),
		...mapMutations(['setPerson']),
		initProvider() {
			const filters = ['weixin', 'qq', 'sinaweibo'];
			uni.getProvider({
				service: 'oauth',
				success: (res) => {
					if (res.provider && res.provider.length) {
						for (let i = 0; i < res.provider.length; i++) {
							if (~filters.indexOf(res.provider[i])) {
								this.providerList.push({
									value: res.provider[i],
									image: '../../static/img/' + res.provider[i] + '.png'
								});
							}
						}
						this.hasProvider = true;
					}
				},
				fail: (err) => {
					console.error('获取服务供应商失败：' + JSON.stringify(err));
				}
			});
		},
		initPosition() {
			/**
			 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			 * 反向使用 top 进行定位，可以避免此问题。
			 */
			this.positionTop = uni.getSystemInfoSync().windowHeight - 120;
		},
		bindLogin() {
			/**
			 * 客户端对账号信息进行一些必要的校验。
			 * 实际开发中，根据业务需要进行处理，这里仅做示例。
			 */
			if (this.account.length < 5) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 5 个字符'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			/**
			 * 实际开发中，使用 uni.request 将账号信息发送至服务端，客户端在回调函数中获取结果信息。
			 */
			const data = {
				account: this.account,
				password: this.password
			};
			let heard = data.account + ":" + data.password;
			let basic = "";
			try {
				basic = Base64.encode(heard);
			} catch (e) {
				//TODO handle the exception
				console.log(JSON.stringify(e))
			}

			basic = Base64.Base64.encode(heard);


			let header = {}
			if (this.code == "") {
				header = {
					"MAXAUTH": basic
				}
				this.postLogin(header);
			} else {

				header = {
					"MAXAUTH": basic
				}
				this.postLogin(header);














			}
		},
		postLogin(header) {
			console.log("登录中----")
			http.post('/mem/applogin/login', {
				PERSONID: this.account
			}, {
				header: header,




				timeout: 30000, // 仅支付宝小程序支持

			}).then(res => {

				console.log("返回值:=========" + JSON.stringify(res.data));

				if (res.data.META.SUCCESS) {
					let userName = res.data.DATA[0].DISPLAYNAME;

					http.setConfig(
						(config) => {
							config.header = {
								"MAXAUTH": header.MAXAUTH,
								"Content-Type": "application/json"
							}
							return config
						}
					);

					
					http.get("/domain").then(ref => {
						uni.setStorage({
							key: "domain",
							data: ref.data.DATA.LIST,
							success: () => {
								console.log("钉钉   域缓存成功")
								this.toMain(userName, this.account.toUpperCase());
							}
						})
					}).catch(err => {
						console.log("域加载错误")
						console.log(JSON.stringify(err))
					});

					/* uni.request({
						url: "https://ser1.itrobot.site/maxrest/rest/domain",
						method: "GET",
						success: (res) => {
							console.log(res.data)
							uni.setStorage({
								key: "domain",
								data: res.data.DATA.LIST,
								success: () => {
									console.log("域缓存成功")
									this.toMain(userName, this.account.toUpperCase());
								}
							})
						},
						fail: (err) => {
							console.log(JSON.stringify(err))
						}
					}); */

















				} else {
					console.log("请求失败了：")
					uni.showToast({
						title: res.data.META.MESSAGE,
						icon: "none"
					})
				}
			}).catch(err => {
				uni.showToast({
					title: "请求出错:" + JSON.stringify(err),
					icon: "none"
				})
			})

		},
		oauth(value) {















			uni.login({
				provider: value,
				success: (res) => {
					console.log(res);
					this.code = res.code;
					uni.showLoading({
						title: "登录中...",
						mask: true
					})
					this.$http.get('/wxlogin', {
						header: {
							"code": this.code
						},
					}).then(res => {
						console.log(res.data);
						if (res.data.META.SUCCESS) {
							let userName = res.data.DATA[0].DISPLAYNAME;
							let personid = res.data.DATA[0].PERSONID;
							this.$http.setConfig(
								(config) => {
									config.header = {
										"MAXAUTH": res.data.DATA[0].PASSWORD,
										"Content-Type": "application/json"
									}
									return config
								}
							);
							//域
							this.$http.get("/domain").then(ref => {
								uni.setStorage({
									key: "domain",
									data: ref.data.DATA.LIST,
									success: () => {
										console.log("域缓存成功")
										uni.hideLoading();
										this.toMain(userName, personid);
									}
								})
							});

						} else {
							uni.showToast({
								title: res.data.META.MESSAGE,
								icon: "none"
							})
						}
					}).catch(err => {
						console.log(JSON.stringify(err))
						this.btTitle = "绑定";
						uni.showToast({
							title: "暂未绑定系统用户请填写用户名密码后提交",
							icon: "none"
						})
					})

					/* uni.getUserInfo({
						provider: value,
						success: (infoRes) => {
				
							this.toMain(infoRes.userInfo.nickName);
						},
						fail() {
							uni.showToast({
								icon: 'none',
								title: '登陆失败'
							});
						}
					}); */
				},
				fail: (err) => {
					console.error('授权登录失败：' + JSON.stringify(err));
				}
			});
		},
		getUserInfo({
			detail
		}) {
			console.log(detail);
			if (detail.userInfo) {

				this.toMain(detail.userInfo.nickName);
			} else {
				uni.showToast({
					icon: 'none',
					title: '登陆失败'
				});
			}
		},
		toMain(userName, personid) {
			this.login(userName, personid);
			this.setPerson(personid);
			/**
			 * 强制登录时使用reLaunch方式跳转过来
			 * 返回首页也使用reLaunch方式
			 */

			uni.setStorage({
				key: "state",
				data: {
					username: userName,
					personid: personid
				},
				success: () => {
					console.log("存成功了")
					uni.switchTab({
						url: '../main/main',
					})
				}
			})








		}
	},
	onReady() {
		this.initPosition();
		this.initProvider();



	},
	onLoad() {
		http = this.$http;
	}
}
